// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
	width: map-get($columns, $numberColumns) - ( ( $columns__margin * ( $numberColumns - 1 ) ) / $numberColumns );
}

// 使用你的斷點變數
@mixin mq($breakpoint) {
	// 檢查斷點是否存在於映射中
	@if map-has-key($breakpoints, $breakpoint) {
	  // 獲取斷點值
	  $bp-value: map-get($breakpoints, $breakpoint);
	  
	  // 生成媒體查詢
	  @media (min-width: $bp-value) {
		@content;
	  }
	} @else {
	  // 如果傳入的是自定義數值而非預定義斷點名稱
	  @media (min-width: $breakpoint) {
		@content;
	  }
	}
  }
  
  // 額外添加向下適配的 mixin (max-width)
  @mixin mq-down($breakpoint) {
	@if map-has-key($breakpoints, $breakpoint) {
	  $bp-value: map-get($breakpoints, $breakpoint);
	  @media (max-width: ($bp-value - 0.0625em)) { // 減去 1px 避免重疊
		@content;
	  }
	} @else {
	  @media (max-width: $breakpoint) {
		@content;
	  }
	}
  }
  
  // 範圍查詢 mixin (介於兩個斷點之間)
  @mixin mq-between($lower, $upper) {
	@if map-has-key($breakpoints, $lower) and map-has-key($breakpoints, $upper) {
	  $lower-value: map-get($breakpoints, $lower);
	  $upper-value: map-get($breakpoints, $upper);
	  @media (min-width: $lower-value) and (max-width: ($upper-value - 0.0625em)) {
		@content;
	  }
	}
  }